import { auth } from "@clerk/nextjs/server";
import { redirect } from "next/navigation";
import { Navbar } from "@/components/navbar";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { User, Clock, Award, FileText, CheckCircle2, XCircle, Clock as ClockIcon } from "lucide-react";
import Link from "next/link";
import { db } from "@/lib/db";
import { users, playerApplications } from "@/lib/db/schema";
import { eq } from "drizzle-orm";

export default async function DashboardPage() {
  const { userId } = await auth();

  if (!userId) {
    redirect("/sign-in");
  }

  // Fetch user data from database
  const userResult = await db.select().from(users).where(eq(users.clerkId, userId)).limit(1);
  
  let applicationStatus: string | null = null;
  let isWhitelisted = false;
  let minecraftUsername = "Not set";

  if (userResult.length > 0) {
    const user = userResult[0];
    isWhitelisted = user.isWhitelisted;
    minecraftUsername = user.minecraftUsername || "Not set";

    // Fetch application if user exists
    const applicationResult = await db
      .select()
      .from(playerApplications)
      .where(eq(playerApplications.userId, user.id))
      .limit(1);

    if (applicationResult.length > 0) {
      applicationStatus = applicationResult[0].status;
    }
  }

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />
      <main className="flex-1 flex items-center justify-center">
        <div className="w-full max-w-[980px] px-4 py-8 md:py-12 lg:py-24 flex flex-col items-center justify-center">
          <div className="flex flex-col items-center gap-4 text-center w-full">
            <h1 className="text-3xl font-bold leading-tight tracking-tighter md:text-5xl lg:leading-[1.1]">
              Dashboard
            </h1>
            <p className="max-w-[750px] text-lg text-muted-foreground">
              Manage your account and view your server status
            </p>
          </div>

          <div className="grid gap-6 mt-8 w-full md:grid-cols-2 lg:grid-cols-3">
            {/* Account Status Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <User className="h-5 w-5 text-primary" />
                  <CardTitle>Account Status</CardTitle>
                </div>
                <CardDescription>Your account information</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Minecraft Username</p>
                  <p className="font-medium">{minecraftUsername}</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Whitelist Status</p>
                  {isWhitelisted ? (
                    <Badge className="bg-green-500">
                      <CheckCircle2 className="h-3 w-3 mr-1" />
                      Whitelisted
                    </Badge>
                  ) : (
                    <Badge variant="secondary">
                      <XCircle className="h-3 w-3 mr-1" />
                      Not Whitelisted
                    </Badge>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Application Status Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <FileText className="h-5 w-5 text-primary" />
                  <CardTitle>Application Status</CardTitle>
                </div>
                <CardDescription>Your server application</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-2">Status</p>
                  {applicationStatus === "pending" && (
                    <Badge variant="secondary">
                      <ClockIcon className="h-3 w-3 mr-1" />
                      Pending Review
                    </Badge>
                  )}
                  {applicationStatus === "approved" && (
                    <Badge className="bg-green-500">
                      <CheckCircle2 className="h-3 w-3 mr-1" />
                      Approved
                    </Badge>
                  )}
                  {applicationStatus === "rejected" && (
                    <Badge variant="destructive">
                      <XCircle className="h-3 w-3 mr-1" />
                      Rejected
                    </Badge>
                  )}
                  {!applicationStatus && (
                    <Badge variant="outline">No Application</Badge>
                  )}
                </div>
                {!applicationStatus && (
                  <Link href="/apply">
                    <Button className="w-full" size="sm">
                      Submit Application
                    </Button>
                  </Link>
                )}
              </CardContent>
            </Card>

            {/* Quick Stats Card */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-2">
                  <Award className="h-5 w-5 text-primary" />
                  <CardTitle>Quick Stats</CardTitle>
                </div>
                <CardDescription>Your server statistics</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Playtime</p>
                  <p className="font-medium">0 hours</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">PVP Kills</p>
                  <p className="font-medium">0</p>
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-1">Rank</p>
                  <Badge variant="outline">Member</Badge>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Recent Activity */}
          <Card className="w-full mt-8">
            <CardHeader>
              <CardTitle>Recent Activity</CardTitle>
              <CardDescription>Your recent server activity</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-center py-8 text-muted-foreground">
                <Clock className="h-12 w-12 mx-auto mb-4 opacity-50" />
                <p>No recent activity</p>
                <p className="text-sm mt-2">Start playing to see your activity here</p>
              </div>
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <div className="w-full mt-8">
            <h2 className="text-2xl font-bold mb-4 text-center">Quick Actions</h2>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              <Link href="/apply">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">Submit Application</CardTitle>
                    <CardDescription>Apply to join the server</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
              <Link href="/leaderboard">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">View Leaderboard</CardTitle>
                    <CardDescription>See top players</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
              <Link href="/events">
                <Card className="hover:bg-accent transition-colors cursor-pointer">
                  <CardHeader>
                    <CardTitle className="text-lg">Upcoming Events</CardTitle>
                    <CardDescription>Check server events</CardDescription>
                  </CardHeader>
                </Card>
              </Link>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}