import { pgTable, text, timestamp, integer, boolean, jsonb, varchar, serial } from "drizzle-orm/pg-core";
import { relations } from "drizzle-orm";

export const users = pgTable("users", {
  id: serial("id").primaryKey(),
  clerkId: varchar("clerk_id", { length: 255 }).notNull().unique(),
  minecraftUsername: varchar("minecraft_username", { length: 16 }),
  email: varchar("email", { length: 255 }).notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
  isWhitelisted: boolean("is_whitelisted").default(false).notNull(),
  isAdmin: boolean("is_admin").default(false).notNull(),
});

export const playerApplications = pgTable("player_applications", {
  id: serial("id").primaryKey(),
  userId: integer("user_id").references(() => users.id),
  minecraftUsername: varchar("minecraft_username", { length: 16 }).notNull(),
  age: integer("age"),
  experience: text("experience"),
  whyJoin: text("why_join").notNull(),
  status: varchar("status", { length: 50 }).default("pending").notNull(), // pending, approved, rejected
  createdAt: timestamp("created_at").defaultNow().notNull(),
  reviewedAt: timestamp("reviewed_at"),
  reviewedBy: integer("reviewed_by").references(() => users.id),
});

export const serverStatistics = pgTable("server_statistics", {
  id: serial("id").primaryKey(),
  date: timestamp("date").defaultNow().notNull(),
  playerCount: integer("player_count").default(0).notNull(),
  maxPlayers: integer("max_players").default(100).notNull(),
  uptime: integer("uptime").default(0).notNull(), // in seconds
  tps: integer("tps").default(20).notNull(),
  ping: integer("ping").default(0).notNull(),
});

export const leaderboardEntries = pgTable("leaderboard_entries", {
  id: serial("id").primaryKey(),
  userId: integer("user_id").references(() => users.id),
  minecraftUsername: varchar("minecraft_username", { length: 16 }).notNull(),
  kills: integer("kills").default(0).notNull(),
  deaths: integer("deaths").default(0).notNull(),
  kdr: integer("kdr").default(0).notNull(), // kills/deaths ratio * 100
  wealth: integer("wealth").default(0).notNull(),
  playtime: integer("playtime").default(0).notNull(), // in minutes
  lastUpdated: timestamp("last_updated").defaultNow().notNull(),
});

export const announcements = pgTable("announcements", {
  id: serial("id").primaryKey(),
  title: varchar("title", { length: 255 }).notNull(),
  content: text("content").notNull(),
  authorId: integer("author_id").references(() => users.id).notNull(),
  isPinned: boolean("is_pinned").default(false).notNull(),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  updatedAt: timestamp("updated_at").defaultNow().notNull(),
});

export const events = pgTable("events", {
  id: serial("id").primaryKey(),
  title: varchar("title", { length: 255 }).notNull(),
  description: text("description"),
  eventType: varchar("event_type", { length: 50 }).notNull(), // tournament, smp_event, community
  startDate: timestamp("start_date").notNull(),
  endDate: timestamp("end_date"),
  location: varchar("location", { length: 255 }),
  maxParticipants: integer("max_participants"),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  createdBy: integer("created_by").references(() => users.id).notNull(),
});

export const waitlistSignups = pgTable("waitlist_signups", {
  id: serial("id").primaryKey(),
  email: varchar("email", { length: 255 }).notNull(),
  minecraftUsername: varchar("minecraft_username", { length: 16 }),
  createdAt: timestamp("created_at").defaultNow().notNull(),
  notified: boolean("notified").default(false).notNull(),
});

// Relations
export const usersRelations = relations(users, ({ many }) => ({
  applications: many(playerApplications),
  leaderboardEntries: many(leaderboardEntries),
  announcements: many(announcements),
}));

export const playerApplicationsRelations = relations(playerApplications, ({ one }) => ({
  user: one(users, {
    fields: [playerApplications.userId],
    references: [users.id],
  }),
  reviewer: one(users, {
    fields: [playerApplications.reviewedBy],
    references: [users.id],
  }),
}));